.file {
  margin-top: 25px;
  font-size: 21px;
  text-align: center;
}

.file-white {
    -webkit-box-shadow: 0 10px 6px -6px rgba(119, 119, 119, 0.5);
       -moz-box-shadow: 0 10px 6px -6px rgba(119, 119, 119, 0.5);
            box-shadow: 0 10px 6px -6px rgba(119, 119, 119, 0.5);
}

.folder-white {
    position:relative;
    -webkit-box-shadow:0 1px 4px rgba(0, 0, 0, 0.3), 0 0 40px rgba(0, 0, 0, 0.1) inset;
       -moz-box-shadow:0 1px 4px rgba(0, 0, 0, 0.3), 0 0 40px rgba(0, 0, 0, 0.1) inset;
            box-shadow:0 1px 4px rgba(0, 0, 0, 0.3), 0 0 40px rgba(0, 0, 0, 0.1) inset;
}
.folder-white:before, .effect8:after {
    content:"";
    position:absolute;
    z-index:-1;
    -webkit-box-shadow:0 0 20px rgba(0,0,0,0.6);
    -moz-box-shadow:0 0 20px rgba(0,0,0,0.6);
    box-shadow:0 0 20px rgba(0,0,0,0.6);
    top:10px;
    bottom:10px;
    left:3px;
    right:3px;
    -moz-border-radius:100px / 10px;
    border-radius:100px / 10px;
}

.file:hover {
  background-color: rgba(102, 163, 198, 0.3);
  border-radius: 0.3em;
  border-color: #E2F1FF;
}
.footer {
  font-size: 0.7em;
}
.button {
  margin-left: 0.5em;
}
.file-white {
  background-color: rgb(189, 222, 235);
  margin-top: 0.6em;
  margin-bottom: 0.6em;
}
.folder-white {
  background-color: rgb(230, 235, 189);
  margin-top: 0.6em;
  margin-bottom: 0.6em;
}
.fileHeader-white {
  background-color: rgb(120, 196, 231);
}
.folderHeader-white {
  background-color: rgb(233, 187, 156);
}
.folderName-white {
  color: rgb(10, 70, 133);
}
.fileName-white {
  color: #464646;
}

.fade-in {
  animation: fadeIn ease .5s;
  -webkit-animation: fadeIn ease .5s;
  -moz-animation: fadeIn ease .5s;
  -o-animation: fadeIn ease .5s;
  -ms-animation: fadeIn ease .5s;
}
@keyframes fadeIn {
  0% {
    opacity:0;
  }
  100% {
    opacity:1;
  }
}

--moz-keyframes fadeIn {
  0% {
    opacity:0;
  }
  100% {
    opacity:1;
  }
}

--webkit-keyframes fadeIn {
  0% {
    opacity:0;
  }
  100% {
    opacity:1;
  }
}

--o-keyframes fadeIn {
  0% {
    opacity:0;
  }
  100% {
    opacity:1;
  }
}
